.pagination{
  display: inline-block;
  padding-left: 0;
  list-style-type: none;
  border: 0.9px solid;
}

.pagination li{
  position: relative;
  float: left;
  padding: 6px 12px;
  background-color: #fff;
  border: 1px solid #ddd;
}

.pagination li a{
  text-decoration:none;
  color: #2c689c;
}

.pagination li:hover {
  background-color:  #218838;
  color: white;
}
